import React, { useMemo } from "react"
import styled from "styled-components"
import SEO from "components/SEO"
import filter from "lodash/filter"

import { graphql } from "gatsby"

import queryString from "query-string"

import Layout from "components/Layout"
import PostList from "components/PostList"
import VerticalSpace from "components/VerticalSpace"

import { title, description, siteUrl } from "../../blog-config"

const EmptyState = styled.div`
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
  min-height: 220px;
  padding: 28px 20px;
  border-radius: ${props => props.theme.radii.md};
  border: 1px dashed ${props => props.theme.colors.border};
  background: linear-gradient(
    135deg,
    ${props => props.theme.colors.surface},
    ${props => props.theme.colors.tagBackground}
  );
  text-align: center;
`

const EmptyStateTitle = styled.p`
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: ${props => props.theme.colors.boldText};
`

const EmptyStateText = styled.p`
  margin: 0;
  font-size: 14px;
  color: ${props => props.theme.colors.secondaryText};
`

const TagsPage = ({ data, location }) => {
  const posts = data.allMarkdownRemark.nodes

  const search = location?.search || ""
  const selected = useMemo(() => queryString.parse(search)["q"], [search])
  const filteredPosts = useMemo(() => {
    if (!selected) return posts

    return filter(
      posts,
      post => post.frontmatter.tags.indexOf(selected) !== -1
    )
  }, [posts, selected])

  return (
    <Layout>
      <SEO title={title} description={description} url={siteUrl} />

      <VerticalSpace size={4} />

      {filteredPosts.length === 0 ? (
        <EmptyState>
          <EmptyStateTitle>검색 결과가 없습니다.</EmptyStateTitle>
          <EmptyStateText>현재 페이지 준비 중 입니다.</EmptyStateText>
        </EmptyState>
      ) : (
        <PostList postList={filteredPosts} />
      )}
    </Layout>
  )
}

export default TagsPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt(pruneLength: 200, truncate: true)
        html
        fields {
          slug
          thumbnail
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          update(formatString: "MMM DD, YYYY")
          title
          tags
          thumbnail
        }
      }
    }
  }
`
